// Dependencies
import React, { useState } from 'react';

// Components
import ProjectCard from '../../components/ProjectCard/ProjectCard';
import MyModal from '../../components/Modal/Modal';

// Images
import NoteApp from '../../images/sharemydine.png'
import Covid from '../../images/currencygram.png'
import ChatApp from '../../images/jkj.png'
import EmployeeDirectory from '../../images/chaatride.png'
import EatDaBurger from '../../images/crypto.png'
import Weather from '../../images/cannabis.png'
import ShelfIndulgence from '../../images/shelf-indulgence.jpg'
import MealMatcher from '../../images/meal-matcher.jpg'

// CSS
import './MyProjects.css'

const MyProjects = () => {

  const projects = [
    {
      src: NoteApp,
      className: 'note-app',
      id: 1,
      title: 'Share My Dine (MERN)',
      text: "Share My Dine is a web platform that connects home cooks with food lovers who want to enjoy authentic and delicious meals. As a senior javascript developer, I was responsible for developing the front-end of the website using React, a popular JavaScript library for building user interfaces. I am proud of this project because it showcases my skills and expertise in JavaScript and React. It also demonstrates my ability to work in a team and deliver high-quality products that meet the client's requirements and expectations.",
      tech: 'React | Redux | Firebase | Tailwind CSS | HTML5 | CSS | Stripe API | Socket.io | GitHub ',
      alt: 'ShareMyDine',
      link: 'https://www.sharemydine.co.uk/',
    },
    {
      src: Covid,
      className: 'pandemic-tracker',
      id: 2,
      title: 'CurrencyGram (MERN)',
      text: "CurrencyGram is a web platform that allows users to compare and exchange currencies online. As a senior javascript developer, I was responsible for developing the back-end of the website using Node.js, a popular JavaScript runtime environment. I am proud of this project because it showcases my skills and expertise in JavaScript and Node.js. It also demonstrates my ability to design, implement, and maintain a scalable and reliable back-end system that supports a dynamic and interactive front-end.",
      tech: 'MongoDB | Express.js | Node.js | RESTful API | Ajax | GitHub',
      alt: 'CurrencyGram',
      link: 'https://currencygram.co.uk/',
    },
    {
      src: ChatApp,
      className: 'chat-app',
      id: 3,
      title: 'JKJ Jewellers (Next.js + Django)',
      text: "JKJ Jewellers is a web platform that offers a wide range of exquisite and elegant jewellery products for various occasions. As a senior javascript developer, I was responsible for developing both the front-end and the back-end of the website using Next.js and Django, two popular frameworks for web development. I am proud of this project because it showcases my skills and expertise in JavaScript, Next.js, Python, and Django. It also demonstrates my ability to create a full-stack web application that meets the client's vision and expectations.",
      tech: 'Next.js | React | Redux | JavaScript | Django | Python | Razorpay API | Cloudinary',
      alt: 'Real time chat application with Socket.io',
      link: 'https://www.jkjjewellers.in/',
    },
    {
      src: EmployeeDirectory,
      className: 'employee-directory',
      id: 4,
      title: 'Chaadride (Angular 11)',
      text: 'This employee directory is built using React.js and is a front end only application. The employee data is imported from a .json file and javascript .map and .sort methods are used to render the data upon search of terms. The app uses class components and changes the state of the list upon search.',
      tech: 'Angular 11 | JavaScript | TypeScript | RxJS | NgRx | Google Maps API | Firebase',
      alt: 'Chaadride',
      link: 'https://chaadride.com/',
    },
    {
      src: EatDaBurger,
      className: 'eat-da-burger',
      id: 5,
      title: 'Exodus (React + Laravel)',
      text: "Exodus is a web platform that offers a service of crypto. As a senior javascript developer, I was responsible for developing both the front-end and the back-end of the website using React and Laravel, two popular frameworks for web development. I am proud of this project because it showcases my skills and expertise in JavaScript, React, PHP, and Laravel. It also demonstrates my ability to create a full-stack web application that meets the client's vision and expectations.",
      tech: 'React | Redux | TypeScript | Bootstrap | Laravel | PHP | Eloquent | MySQL | Stripe API',
      alt: 'Exodus',
      link: 'https://www.exodus.com/',
    },
    {
      src: Weather,
      className: 'weather-dashboard',
      id: 6,
      title: 'Matrue Cannabis (React + Three.js + Node.js)',
      text: "Matrue Cannabis is a web platform that provides information and education about cannabis and its benefits. As a senior javascript developer, I was responsible for developing both the front-end and the back-end of the website using React, Three.js and Node.js. It also demonstrates my ability to work in a team and deliver high-quality products that meet the client's requirements and expectations. ",
      tech: 'JavaScript | TypeScript | React | Redux | Node.js | Express.js | MongoDB | Firebase | Three.js | HTML | CSS | GitHub',
      alt: 'Matrue Cannabis',
    },
    {
      src: ShelfIndulgence,
      className: 'shelf-indulgence',
      id: 7,
      title: 'Shelf Indulgence',
      text: 'A book club community where book enthusiasts can join, login, and search for books via an API and discuss the books they are reading in a real time chat application. Search for books using an api, add those books to a database of books you want to read, and keep track of books you have read.',
      tech: 'JavaScript | MySQL and Sequelize | Passport Authentication | Express.js | Socket.io | Heroku',
      alt: 'Shelf Indulgence Book Club',
      link: 'https://shelf-indulgence-bc.herokuapp.com/',
    },
    {
      src: MealMatcher,
      className: 'meal-matcher',
      id: 8,
      title: 'Meal Matcher',
      text: 'Meal Matcher is a recipe application using an API to render recipe data when a user searched for an ingredient.',
      tech: 'JavaScript | RapidAPI | GitHub',
      alt: 'Meal Matcher API',
    },

  ]

  const [modalShow, setModalShow] = useState(false);

  const [data, setData] = useState({})

  const onCardClick = (item) => {
    setModalShow(true)
    setData(item)
  }

  return (
    <div className="projects-container col-12" id='projects-container'>
      <div className='projects-wrapper'>
        <div className='row col-12' style={{ margin: '0 auto', width: '100%' }}>
          <h1 className='display-3 text-center projects-title'>My Projects</h1>
        </div>
        <div className='row' style={{ margin: '0 auto', width: '90%' }}>
          {projects.map((item, i) => (
            <ProjectCard
              {...projects}
              key={i}
              src={item.src}
              alt={item.alt}
              id={item.id}
              text={item.text}
              title={item.title}
              className={item.className}
              onClick={() => onCardClick(item)}
            />
          ))}
        </div>
        <MyModal
          show={modalShow}
          onHide={() => setModalShow(false)}
          {...data}
        />
      </div>
    </div>
  );
}

export default MyProjects;