import React from 'react';
import { AiFillGithub } from 'react-icons/ai'
import { FaLinkedinIn, FaTwitter } from 'react-icons/fa'

// Containers
import Container from '../../components/Container/Container';

//Components

//Images
import Jared from '../../images/logo.png'

//CSS 
import './Footer.css'

function Footer(props) {
  return (
    <Container className='footer-container col-12' id='footer-container'>
      <div className='row' style={{ padding: '20px 0px', height: '100%' }}>
        <div className='about-me col-md-12 col-lg-6'>
          <img className='jared-img' src={Jared} alt='Dmytro Redkinin' />
          <p className='about-me-text'>Dmytro is a talented senior javascript developer who worked on my website using React and Node. He was very skilled, reliable, and attentive to detail. He created a beautiful and interactive website that met all my requirements and expectations. He was also very friendly, responsive, and helpful throughout the project. I was very satisfied with his work and would highly recommend him.</p>
        </div>
        <div className='social-links col-2 d-flex flex-row-reverse'>
          <ul>
            <a href="https://github.com/dmytroredkinin0710" target='_blank'><li className="footer-list-item"><AiFillGithub className="footer-icon github-icon" /></li></a>

            <a href="https://www.linkedin.com/in/dmytro-redkinin-998236297/" target='_blank'><li className="footer-list-item"><FaLinkedinIn className="footer-icon linkedin-icon" /></li></a>

          </ul>
        </div>
        <div className='page-links col-2 d-flex flex-row-reverse'>
          <ul>
            <a href='#home-container' ><li>Home</li></a>
            <a href='/signup' target='_blank'><li>Signup</li></a>
            <a href='/login' target='_blank'><li>Login</li></a>
          </ul>
        </div>
      </div>
    </Container>
  );
}

export default Footer;